import React from "react";
import proPic from "./proPicR.png";

export default function About() {
  return (
    <section id="about">
      <div className="container mx-auto flex px-10 py-20 md:flex-row flex-col items-center">
        <div className="lg:flex-grow md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-left mb-16 md:mb-0 items-center text-center">
          <h1 className="title-font sm:text-4xl text-3xl mb-4 font-medium text-white">
            Hi, I'm Karl.
            <br className="hidden lg:inline-block" /> I build apps with
            Javascript.
          </h1>
          <p className="mb-8 leading-relaxed">
            I am currently a student at the University of Washington Coding Bootcamp and eager
            to go to work as a developer. I will be graduating in November of 2022.
          </p>
          <div className="flex justify-center">
            <a
              href="#contact"
              className="inline-flex text-white bg-indigo-400 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 rounded text-lg"
            >
              Contact Me
            </a>
            <a
              href="#projects"
              className="ml-4 inline-flex text-white bg-indigo-400 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 hover:text-white rounded text-lg"
            >
              See My Apps and Projects
            </a>
          </div>
        </div>
        <div className="lg:max-w-lg lg:w-full md:w-1/2 w-5/6">
          <img
            className="object-cover object-center z-0 rounded-lg"
            alt="headshot"
            src={proPic}
          />
        </div>
      </div>
    </section>
  );
}
